  | 'state-active'
  | 'state-focus'
  | 'state-disabled'
  | 'external-facebook'
  | 'external-google'
  | 'external-instagram'
  | 'external-linkedin'
  | 'external-pinterest'
  | 'external-twitter'
  | 'external-wechat'
